import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/preloader";
import ProfileStatusWithHooks from "../ProfileStatusWithHooks";
import avatarPhoto from './../../../assets/images/avatarPhoto.jpg'

import React from "react";
import ShowMore from "../../common/Showmore";
import AdditionalInformation from "../Additionalnformation";


const ProfileInfo = (props) => {

    if (!props.profile) {
        return <Preloader/>
    }

    const onMainAvatarSelector = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }



    return (
        <div>
            {/* <div className={s.fon}>
                <img
                    src="https://images.ctfassets.net/hrltx12pl8hq/7yQR5uJhwEkRfjwMFJ7bUK/dc52a0913e8ff8b5c276177890eb0129/offset_comp_772626-opt.jpg?fit=fill&w=800&h=300"
                    alt="first-photo"/>
            </div>*/}
            <div className={s.descriptionBlock}>
                <img src={props.profile.photos.large || avatarPhoto} className={s.avatar} alt="photo"/>
                {props.isOwner && <input type={"file"} onChange={onMainAvatarSelector}/>}
                <div className={s.info}>
                <ProfileStatusWithHooks className={s.status} status={props.status} updateStatus={props.updateStatus} />
                    {ShowMore(<AdditionalInformation profile={props.profile} />)}
                </div>
            </div>
        </div>
    );
}



export default ProfileInfo;
