import React, {useState} from "react";
import AdditionalInformation from "../Profile/Additionalnformation";

const ShowMore = (props) => {
    let [show, toggleShow] = useState(false);
    return (
    <div>
        <button onClick={() => toggleShow(!show)}>
            {show ? "Hide" : "Show additional information"}
        </button>
        {show && <AdditionalInformation profile={props.props.profile} />}
    </div>)
}

export default ShowMore;